
import { defineComponent } from "vue";
import { IonButton } from "@ionic/vue";
export default defineComponent({
  components: { IonButton },
  props: {
    btnSize: {
      type: String,
      default: '90%'
    },
    layout: {
      type: Array,
      required: true,
      default: () => []
    },
    onKeyPress: {
      type: Function,
      required: true,
    },
  },
  methods: {
    keyPress(key: any) {
      this.onKeyPress(key);
    },
    dynamicClass(row: any){
      if(row.length == 3 && row[0] == "Unknown")
        return "Keypad"
      return row[0]
    }
  },
});
